import { Container, Typography, Card, Box, Grid, Link, Divider } from '@mui/material'
import dayjs from 'dayjs'
import { FC } from 'react'
import { Helmet } from 'react-helmet-async'
import { contributors } from 'src/data/contributors'
import GitHubIcon from '@mui/icons-material/GitHub'

const ContributorCard = ({ contributor }) => {
  const startAtDate = dayjs(contributor.startAt)
  const endAtDate = dayjs(contributor.endAt)

  return (
    <Grid item xs={12} sm={4}>
      <Card sx={{ m: 1, p: 2, height: '100px', backgroundColor: '#f2f2f2' }}>
        <Box mb={0.5}>
          <Typography variant="h4" color="primary" component="span">
            {contributor.name}{' '}
            <Typography variant="h5" component="span" color="black">
              {contributor.position}
            </Typography>
          </Typography>
        </Box>
        <Typography>
          {startAtDate.format('YYYY.MM')} ~ {contributor.endAt && endAtDate.format('YYYY.MM')}
        </Typography>
        <Typography>{contributor.note}</Typography>
        <Link href={contributor.github} target="_blank" rel="noopener noreferrer" sx={{ color: 'black' }}>
          <GitHubIcon />
        </Link>
      </Card>
    </Grid>
  )
}

const Contributors: FC<void> = () => {
  const calcPeriod = contributor => {
    const benefitList = ['김재민', '김규진', '김승현', '전병국']
    const startAt = new Date(contributor.startAt)
    const endAt = contributor.endAt ? new Date(contributor.endAt) : new Date()

    let months = Math.round((endAt.getTime() - startAt.getTime()) / (1000 * 60 * 60 * 24 * 30))

    if (benefitList.includes(contributor.name)) {
      months = months + 1
    }

    return months
  }

  const sortedContributors = contributors.sort((a, b) => calcPeriod(b) - calcPeriod(a))

  return (
    <>
      <Helmet>
        <title>Contributors</title>
      </Helmet>
      <Container maxWidth="md">
        <Card elevation={0}>
          <Box p={2} pt={4}>
            <Typography variant="h3" textAlign="center" gutterBottom>
              Developed by <Link href="https://kwebofficial.com">KWEB</Link>
            </Typography>
            <Typography variant="h5" align="center">
              문의 및 버그 제보 : officialkweb@gmail.com
            </Typography>
          </Box>
          <Divider sx={{ m: 1 }} />
          <Typography variant="h3" color="black" align="center" m={2}>
            Contributors
          </Typography>
          <Box sx={{ p: 2 }}>
            <Grid container spacing={1}>
              {sortedContributors.map((c, index) => (
                <ContributorCard contributor={c} key={index} />
              ))}
            </Grid>
          </Box>
        </Card>
      </Container>
    </>
  )
}

export default Contributors
